import navBar from "../common/Navbar/navbar.vue" //
import Phone from "@/components/uni-phone/index.vue" //
import popMask from "./components/popMask.vue" //
import popCart from "./components/popCart.vue" //
import dishDetail from "./components/dishDetail.vue" //
import {
	// 
	userLogin,
	getCategoryList,
	dishListByCategoryId,
	// 
	querySetmeaList,
	// 
	getShoppingCartList,
	// 
	newAddShoppingCartAdd,
	// 
	newShoppingCartSub,
	// 
	delShoppingCart,
	// 
	querySetmealDishById,
	// 
	getShopStatus,
	// 
	getMerchantInfo,
} from "../api/api.js"
import { mapState, mapMutations } from "vuex"
import { baseUrl } from "../../utils/env"
export default {
	data() {
		return {
			title: "Hello",
			// 
			openOrderCartList: false,
			// 
			typeListData: [],
			dishListData: [],
			// 
			dishListItems: [],
			dishDetailes: {},
			openDetailPop: false,
			openMoreNormPop: false,
			moreNormDataes: null,
			tableInfo: null,
			moreNormDishdata: null,
			moreNormdata: null,
			// 
			dishMealData: null,
			openTablePeoPleNumber: 1,
			orderData: 0,
			// 
			typeIndex: 0,
			// 
			openTablePop: false,
			// 
			flavorDataes: [],
			// 
			orderDishNumber: 0,
			// 
			orderDishPrice: 0,
			params: {
				shopId: "f3deb",
				storeId: "1282344676983062530",
				tableId: "1282346960773238786",
			},
			// numberid --- idid
			rightIdAndType: {},
			phoneData: "",
			tablewareNumber: 0,
			shopStatus: null,
			scrollTop: 0,
			menuHeight: 0, // 
			menuItemHeight: 0, // item
			itemId: "", // scroll-viewid
			arr: [],
		}
	},
	//   
	components: {
		navBar,
		Phone,
		popMask,
		popCart,
		dishDetail,
	},
	//   
	computed: {
		// 
		orderListDataes: function () {
			return this.orderListData()
		},
		loaddingSt: function () {
			return this.lodding()
		},
		// 
		orderAndUserInfo: function () {
			let orderData = []
			Array.isArray(this.orderListDataes) &&
				this.orderListDataes.forEach((n, i) => {
					let userData = {}
					userData.nickName = n.name ?? ""
					userData.avatarUrl = n.image ?? ""
					userData.dishList = [n]
					const num = orderData.findIndex(
						(o) => o.nickName == userData.nickName
					)
					if (num != -1) {
						orderData[num].dishList.push(n)
					} else {
						orderData.push(userData)
					}
				})
			return orderData
		},
		ht: function () {
			return (
				uni.getMenuButtonBoundingClientRect().top +
				uni.getMenuButtonBoundingClientRect().height +
				7
			)
		},
	},

	onReady() {
		this.getMenuItemTop()
	},
	onLoad(options) {
		uni.onNetworkStatusChange(function (res) {
			if (res.isConnected == false) {
				uni.navigateTo({
					url: "/pages/nonet/index",
				})
			}
		})
		if (options) {
			if (!options.status && !options.formOrder) {
				this.getData()
			}
		}
	},
	onShow() {
		if (this.token()) {
			this.init()
		}
	},
	methods: {
		//   vuex
		...mapMutations([
			"setShopInfo", //
			"setShopPhone", //
			"setShopStatus", //
			"initdishListMut", //
			"setStoreInfo",
			"setBaseUserInfo", //
			"setLodding",
			"setToken", //token
			"setDeliveryFee", //
			"setAddress",	// 
		]),
		// vuex
		...mapState([
			"shopInfo", //
			"shopPhone", //
			"orderListData",
			"baseUserInfo", //
			"lodding",
			"token", //token
			"deliveryFee", //
			"addressDataMap", // 
		]),
		loginSync() {
			return new Promise((resolve, reject) => {
				uni.login({
					success: (loginRes) => {
						if (loginRes.errMsg === "login:ok") {
							resolve(loginRes.code)
							console.log(loginRes)
						}
					},
				})
			})
		},
		// 
		getData() {
			let res = wx.getMenuButtonBoundingClientRect()
			let _this = this
			// 
			this.getShopInfo()
			this.selectHeight = res.height
			if (this.token() === "") {
				uni.showModal({
					title: "",
					content: "",
					showCancel: false,
					success(res) {
						if (res.confirm) {
							let jsCode = ""
							uni.login({
								provider: 'weixin',
								success: (loginRes) => {
									if (loginRes.errMsg === 'login:ok') {
										jsCode = loginRes.code
									}
								},
							})
							// 
							uni.getUserProfile({
								desc: '',
								success: function (userInfo) {
									_this.setBaseUserInfo(userInfo.userInfo)
									const params = {
										code: jsCode,
										// 
									}
									// 
									uni.getLocation({
										type: 'gcj02', isHighAccuracy: true
									}).then(([err, result]) => {
										if (err) {
											uni.showToast({
												title: "",
												icon: "none"
											})
										} else {
											if (process.env.NODE_ENV === '"development"') {
												params.location = `116.481488,39.990464`//	
											} else {
												params.location = `${result.longitude},${result.latitude}`
											}

											userLogin(params)
												.then((success) => {
													if (success.code === 1) {
														_this.setToken(success.data.token)
														console.log(success.data.token)
														// 
														_this.setDeliveryFee(1)
														// 
														_this.setShopInfo({
															shopName: success.data.shopName,
															shopAddress: success.data.shopAddress,
															shopId: success.data.shopId,
														})
														_this.init()
													}
												})
												.catch((err) => {
													console.log("fail")
												})
											


										}

									})

								},
								fail: function (err) { },
							})
						}
					},
				})
			}
		},

		async init() {
			// 
			if (this.typeIndex !== 0) {
				this.typeIndex = 0
			}

			// 
			this.getMerchantInfo()
			getCategoryList().then((res) => {
				if (res && res.code === 1) {
					this.typeListData = [...res.data]
					if (res.data.length > 0) {
						this.getDishListDataes(res.data[this.typeIndex || 0])
					}
				}
			})
			// ---
			this.getTableOrderDishListes()
			this.$store.dispatch('fetchAddressData')
		},
		// 
		async swichMenu(params, index) {
			if (this.arr.length == 0) {
				await this.getMenuItemTop()
			}
			if (index == this.typeIndex) return
			this.$nextTick(function () {
				this.typeIndex = index
				this.leftMenuStatus(index)
			})
			this.getDishListDataes(params, index)
		},
		// 
		getElRect(elClass, dataVal) {
			new Promise((resolve, reject) => {
				const query = uni.createSelectorQuery().in(this)
				query
					.select("." + elClass)
					.fields(
						{
							size: true,
						},
						(res) => {
							// resnull
							if (!res) {
								setTimeout(() => {
									this.getElRect(elClass)
								}, 10)
								return
							}
							this[dataVal] = res.height
							resolve()
						}
					)
					.exec()
			})
		},
		// 
		async leftMenuStatus(index) {
			this.typeIndex = index
			// 0
			if (this.menuHeight == 0 || this.menuItemHeight == 0) {
				await this.getElRect("menu-scroll-view", "menuHeight")
				await this.getElRect("type_item", "menuItemHeight")
			}
			// item
			this.scrollTop =
				index * this.menuItemHeight +
				this.menuItemHeight / 2 -
				this.menuHeight / 2
		},
		// item
		getMenuItemTop() {
			new Promise((resolve) => {
				let selectorQuery = uni.createSelectorQuery()
				selectorQuery
					.selectAll(".class-item")
					.boundingClientRect((rects) => {
						// rects[](selectAll)
						if (!rects.length) {
							setTimeout(() => {
								this.getMenuItemTop()
							}, 10)
							return
						}
					})
					.exec()
			})
		},
		// 
		async getDishListDataes(params, index) {
			this.rightIdAndType = {}
			this.rightIdAndType = {
				id: params.id,
				type: params.type,
			}
			const param = {
				categoryId: params.id,
			}
			// type12
			if (params.type === 1) {
				await dishListByCategoryId(param)
					.then((res) => {
						if (res && res.code === 1) {
							// number ----- newCardNumber
							this.dishListData =
								res.data &&
								res.data.map((obj) => ({
									...obj,
									type: 1,
									newCardNumber: 0,
								}))
						}
					})
					.catch((err) => { })
			} else {
				// 
				await querySetmeaList(param)
					.then((success) => {
						if (success && success.code === 1) {
							// dishListItems---this.dishListData
							this.dishListData =
								success.data &&
								success.data.map((obj) => ({
									...obj,
									type: 2,
									newCardNumber: 0,
								}))
						}
					})
					.catch((err) => { })
			}
			this.typeIndex = index
			this.setOrderNum()
		},
		// 
		async getShopInfo() {
			await getShopStatus()
				.then((res) => {
					this.shopStatus = res.data
					console.log(res.data);
					this.setShopStatus(res.data)
				})
				.catch((err) => { })
		},
		// 
		async getMerchantInfo() {
			await getMerchantInfo()
				.then((res) => {
					this.phoneData = res.data.phone
					console.log(res);
					this.setShopPhone(res.data)
				})
				.catch((err) => { })
		},
		// image
		getNewImage(image) {
			return `${baseUrl}/common/download?name=${image}`
		},
		// 
		async getTableOrderDishListes() {
			// 
			await getShoppingCartList({})
				.then((res) => {
					if (res.code === 1) {
						this.initdishListMut(res.data)
						this.computOrderInfo()
					}
				})
				.catch((err) => { })
		},
		// 
		goOrder() {
			uni.navigateTo({
				url: "/pages/order/index",
			})
		},
		//  - 
		async addDishAction(item, form) {
			// 
			if (
				this.openMoreNormPop &&
				(!this.flavorDataes || this.flavorDataes.length <= 0)
			) {
				uni.showToast({
					title: "",
					icon: "none",
				})
				return false
			}
			this.openMoreNormPop = false
			// obj.newCardNumber----number
			this.tablewareNumber++
			this.dishDetailes.dishNumber++
			if (
				this.orderListDataes &&
				!this.orderListDataes.some((n) => n.id == item.dishId) &&
				this.flavorDataes.length > 0
			) {
				item.flavorRemark = JSON.stringify(this.flavorDataes)
			}
			// sort
			let dishFlavorDatas = ""
			let flavorRemark = []
			if (item.flavorRemark) {
				flavorRemark = JSON.parse(item.flavorRemark)
			}
			if (item.dishFlavor !== "" && item.dishFlavor) {
				dishFlavorDatas = item.dishFlavor
			} else if (flavorRemark.length > 0) {
				dishFlavorDatas = flavorRemark.join(',')
			} else {
				dishFlavorDatas = null
			}
			let params = {
				dishFlavor: dishFlavorDatas,
			}
			if (item.type === 1) {
				params = {
					...params,
					dishId: item.id,
				}
			} else if (item.type === 2) {
				params = {
					setmealId: item.id,
				}
			} else if (form === "") {
				if (item.dishId) {
					params = {
						...params,
						dishId: item.dishId,
					}
				} else {
					params = {
						setmealId: item.setmealId,
					}
				}
			}
			newAddShoppingCartAdd(params)
				.then((res) => {
					if (res.code === 1) {
						// ---
						this.getTableOrderDishListes()
						// 
						this.getDishListDataes(this.rightIdAndType)
						this.flavorDataes = []
					}
				})
				.catch((err) => { })
		},
		// 
		addShop(item) {
			console.log(item);
			this.dishDetailes = item
			this.addDishAction(item, "")
		},
		//  - 
		async redDishAction(item, form) {
			// obj.newCardNumber----number
			this.tablewareNumber--
			this.dishDetailes.dishNumber--
			let dishFlavorDatas = ""
			let flavorRemark = []
			if (item.flavorRemark) {
				flavorRemark = JSON.parse(item.flavorRemark)
			}
			if (item.dishFlavor !== "" && item.dishFlavor) {
				dishFlavorDatas = item.dishFlavor
			} else if (flavorRemark.length > 0) {
				dishFlavorDatas = flavorRemark[0]
			} else {
				dishFlavorDatas = null
			}
			let params = {
				dishFlavor: dishFlavorDatas,
			}
			if (item.type === 1) {
				params = {
					...params,
					dishId: item.id,
				}
			} else if (item.type === 2) {
				params = {
					// ...params,
					setmealId: item.id,
				}
			} else if (form === "") {
				if (item.dishId) {
					params = {
						...params,
						dishId: item.dishId,
					}
				} else {
					params = {
						setmealId: item.setmealId,
					}
				}
			}
			await newShoppingCartSub(params)
				.then((res) => {
					if (res.code === 1) {
						// ---
						this.getTableOrderDishListes()
						// 
						this.getDishListDataes(this.rightIdAndType)
					}
				})
				.catch((err) => { })
		},
		// 
		clearCardOrder() {
			delShoppingCart()
				.then((res) => {
					this.openOrderCartList = false
					// ---
					this.getTableOrderDishListes()
					// 
					this.getDishListDataes(this.rightIdAndType)
				})
				.catch((err) => { })
		},
		// 
		openDetailHandle(item) {
			this.dishDetailes = item
			if (item.type === 2) {
				querySetmealDishById({
					id: item.id,
				})
					.then((res) => {
						if (res.code === 1) {
							this.openDetailPop = true
							this.dishMealData = res.data
						}
					})
					.catch((err) => { })
			} else {
				this.openDetailPop = true
			}
		},
		// 
		dishClose() {
			this.openDetailPop = false
		},
		// 
		moreNormDataesHandle(item) {
			this.flavorDataes.splice(0)
			this.moreNormDishdata = item
			this.openDetailPop = false
			this.openMoreNormPop = true
			this.moreNormdata = item.flavors.map((obj) => ({
				...obj,
				value: JSON.parse(obj.value),
			}))
			this.moreNormdata.forEach((item) => {
				if (item.value && item.value.length > 0) {
					this.flavorDataes.push(item.value[0])
				}
			})
		},
		//  
		checkMoreNormPop(val) {
			let obj = val.obj
			let item = val.item
			let ind
			let findst = obj.some((n) => {
				ind = this.flavorDataes.findIndex((o) => o == n)
				return ind != -1
			})
			const num = this.flavorDataes.findIndex((it) => it == item)
			if (num == -1 && !findst) {
				this.flavorDataes.push(item)
			} else if (findst) {
				this.flavorDataes.splice(ind, 1)
				this.flavorDataes.push(item)
			} else {
				this.flavorDataes.splice(num, 1)
			}
		},
		// 
		closeMoreNorm(moreNormDishdata) {
			this.flavorDataes.splice(0, this.flavorDataes.length)
			this.openMoreNormPop = false
		},
		// 
		computOrderInfo() {
			let oriData = this.orderListDataes
			this.orderDishNumber = this.orderDishPrice = 0
			oriData.map((n, i) => {
				this.orderDishNumber += n.number
				this.orderDishPrice += n.number * n.amount
			})
			this.orderDishPrice = this.orderDishPrice
		},
		//  - 
		setOrderNum() {
			let ODate = this.dishListData
			let CData = this.orderListDataes
			ODate &&
				ODate.map((obj, index) => {
					obj.dishNumber = 0
					// 
					if (obj.flavors) {
						obj.flavors.forEach((value, i) => {
							if (value.name === "") {
								obj.flavors.splice(i, 1)
							}
						})
					}

					if (CData.length > 0) {
						CData &&
							CData.forEach((tg, ind) => {
								if (obj.id === tg.dishId) {
									obj.dishNumber = tg.number
								}
								if (obj.id === tg.setmealId) {
									obj.dishNumber = tg.number
								}
							})
					}
				})
			if (this.dishListItems.length == 0) {
				this.dishListItems = ODate
			} else {
				this.dishListItems.splice(0, this.dishListItems.length, ...ODate)
			}
		},
		// 
		handlePhone(type) {
			this.$refs.phone.$refs.popup.open(type)
		},
		// 
		closePopup(type) {
			this.$refs.phone.$refs.popup.close(type)
		},
		disabledScroll() {
			return false
		},
	},
}
