export const statusWord = (status, time) => {
	if (time) {
		if (status === 1 && time > 0) {
			return ''
		} else if (status === 6 || (time < 0 && status === 1)) {
			return ''
		}
	}
	switch (status) {
		case 1:
			return ''
		case 2:
			return ''
		case 3:
			return ''
		case 4:
			return ''
		case 5:
			return ''
		case 6:
			return ''
		case 7:
			return ''
	}
}
